@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-image: url(/react-expenses-neon/static/media/bg.173b1cf3.png);
  background-size: cover;
  overflow-x: hidden;
}

.container {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
}

input,
select {
  font-size: 1rem;
  color: white;
  background: none;
  border: none;
  outline: none;
  box-sizing: border-box;
}

select {
  cursor: pointer;
}

option {
  color: black;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

@media (min-width: 580px) {
  input {
    font-size: 1.25rem;
  }
}

button {
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
}

button:focus,
button:active {
  outline: none;
}

button svg {
  width: 24px;
  height: 24px;
  fill: white;
}

.card {
  max-width: 1000px;
  margin: 0 auto;
  padding: 24px;
  background-color: rgb(20, 127, 177, 0.1);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  border-radius: 12px;
}

.add-expense__form {
  display: flex;
  align-items: center;
}

.add-expense__controls {
  display: flex;
  grid-gap: 2%;
  gap: 2%;
  grid-row-gap: 16px;
  row-gap: 16px;
  flex-wrap: wrap;
  flex-grow: 1;
}

.add-expense__control {
  display: flex;
  flex-direction: column;
  width: 31%;
}

@media (max-width: 800px) {
  .add-expense__control {
    width: 43%;
  }
}
@media (max-width: 600px) {
  .add-expense__control {
    width: 100%;
  }
}

.add-expense__control label {
  color: white;
}

.add-expense__control input {
  border-bottom: 1px solid white;
  width: 100%;
  height: 30px;
}
.add-expense__control input::-webkit-calendar-picker-indicator {
  -webkit-filter: invert(1);
          filter: invert(1);
}

.add-expense__actions {
  padding-left: 24px;
}
.add-expense__actions button {
  cursor: pointer;
  background: linear-gradient(
    -65deg,
    rgba(255, 255, 255, 0.15),
    rgba(136, 200, 255, 0.3)
  );
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  padding: 0.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  line-height: 1;
  margin: 0;
  padding: 8px;
  transition: 0.25s;
  box-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #88c8ff,
    0 0 45px #88c8ff, 0 0 55px #88c8ff, 0 0 70px #88c8ff, 0 0 80px #88c8ff;
}

.add-expense__actions button:hover {
  box-shadow: 0 0 6px #fff, 0 0 12px #fff, 0 0 18px #fff, 0 0 30px #88c8ff,
    0 0 45px #88c8ff, 0 0 55px #88c8ff, 0 0 70px #88c8ff, 0 0 80px #88c8ff;
}

.add-expense__actions button svg {
  fill: white;
  width: 24px;
  height: 24px;
  -webkit-filter: drop-shadow(0 0 6px #fff);
          filter: drop-shadow(0 0 6px #fff);
}

.add-expense__error {
  width: 100%;
  color: red;
  text-align: center;
  margin-top: 8px;
}

@media (max-width: 600px) {
  .add-expense__error {
    text-align: left;
  }
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background: linear-gradient(
    -65deg,
    rgba(255, 255, 255, 0.15),
    rgba(136, 200, 255, 0.3)
  );
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  padding: 0.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  text-align: center;
  display: flex;
}

.expense-item__price span {
  cursor: default;
}

@media (min-width: 580px) {
  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}


.expense-item__date {
  color: white;
  padding: 8px;
  text-align: center;
  border-radius: 12px;
  background: linear-gradient( -65deg, rgba(255, 255, 255, 0.15), rgba(136, 200, 255, 0.3));
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  font-size: 10px;
  width: 80px;
}
.expense-item__date > div {
 margin-right: 4px;
}
.expense-item__date > div:first-child {
  font-size: 40px;
  line-height: 1;
  font-weight: bold;
}
.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 1rem 0 rgba(255, 255, 255, .2); 
  padding: 16px;
  margin: 1rem 0;
  border-radius: 12px;
  background: linear-gradient( -65deg, rgba(255, 255, 255, 0.15), rgba(136, 200, 255, 0.3));
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  width: 100%;
}

.expense-item__container {
  width: 100%;
}

.expense-item__description {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  width: 100%;
}

@media (max-width: 580px) {
  .expense-item__description {
    flex-direction: column-reverse;
    align-items: flex-end;
    text-align: right;
  }
}



.expense-item__actions button {
  cursor: pointer;
  background: linear-gradient(
    -65deg,
    rgba(255, 255, 255, 0.15),
    rgba(136, 200, 255, 0.3)
  );
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  padding: 0.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  line-height: 1;
  margin: 0;
  padding: 8px;
  transition: 0.25s;
  box-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #88c8ff,
    0 0 45px #88c8ff, 0 0 55px #88c8ff, 0 0 70px #88c8ff, 0 0 80px #88c8ff;
}

.expense-item__actions button:hover {
  box-shadow: 0 0 6px #fff, 0 0 12px #fff, 0 0 18px #fff, 0 0 30px #88c8ff,
    0 0 45px #88c8ff, 0 0 55px #88c8ff, 0 0 70px #88c8ff, 0 0 80px #88c8ff;
}

.expense-item__actions button svg {
  fill: white;
  width: 24px;
  height: 24px;
  -webkit-filter: drop-shadow(0 0 6px #fff);
          filter: drop-shadow(0 0 6px #fff);
}
.expense-item__title {
  margin: 0 2rem;
  margin-right: 0;
  max-width: 100%;
  width: 100%;
}

.expense-item__title span {
  color: white;
  font-size: 24px;
  text-shadow: 0 0 1px #fff, 0 0 2px #fff, 0 0 4px #88c8ff, 0 0 10px #88c8ff,
    0 0 14px #88c8ff, 0 0 35px #88c8ff, 0 0 40px #88c8ff;
}

@media (max-width: 600px) {
  .expense-item__title span {
    font-size: 18px;
  }
}

.expenses-panel__column-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.expenses-panel__column {
  height: 80px;
  width: 24px;
  border-radius: 12px;
  background-color: rgb(20, 127, 177, 0.1);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  position: relative;
}

.expenses-panel__column-overflow {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 12px;
}

.expenses-panel__column-fill {
  background: white;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  position: absolute;
  left: 0;
  bottom: 0;
}

.expenses-panel__column-fill-shadow {
  background: white;
  width: 100%;
  border-radius: 12px;
  box-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #88C8FF,
    0 0 45px #88C8FF, 0 0 55px #88C8FF, 0 0 70px #88C8FF, 0 0 80px #88C8FF;
    position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.expenses-panel__month {
  color: white;
}
.expenses-panel__amount {
  color: white;
}

.expenses-panel__header {
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.expenses-panel__total-amount {
  color: white;
  font-size: 40px;
}

.expenses-panel__filter select {
  font-size: 40px;
}

@media (max-width: 600px) {
  .expenses-panel__total-amount {
    font-size: 28px;
  }

  .expenses-panel__filter select {
    font-size: 32px;
  }
}

.expenses-panel__columns {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 8px;
}

@media (max-width: 600px) {
  .expenses-panel__columns {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media (max-width: 400px) {
  .expenses-panel__columns {
    grid-template-columns: repeat(4, 1fr);
  }
}
